/* harmony default export */ __webpack_exports__["default"] = ("<div class=\"content\" role=\"main\" align=\"center\">\n    <div class=\"tab-content responsive col-sm-8 hidden-xs hidden-sm\" align=\"left\">\n        <div id=\"tab1\" class=\"tab-pane active\">\n            <h2>Quin es el aliado?</h2>\n            <p dir=\"ltr\"><img\n                    src=\"../../../assets/plink-logo.png\">\n            </p>\n    \n            <p dir=\"ltr\">Plink es una herramienta de Bancolombia creada para ayudarte a mejorar tus ventas. Te permite\n                conocer el <strong>comportamiento de tu negocio, el perfil detallado de tus clientes y compararte con tu\n                    competencia y tu categora.</strong></p>\n    \n            <h3 dir=\"ltr\">Cmo funciona?</h3>\n    \n            <p dir=\"ltr\">Interpreta los datos transaccionales de tu negocio, entregndote informacin de forma agregada y\n                anonimizada, por medio de grficas que ayudan a facilitar el entendimiento de la informacin de tus ventas y\n                tus compradores, para que tomes decisiones que impacten positivamente en las ventas de tu negocio.</p>\n    \n            <h3 dir=\"ltr\">Para qu sirve?</h3>\n    \n            <ul class=\"list-basic\" dir=\"ltr\">\n                <li>Anticiparse a las oportunidades del mercado.</li>\n                <li>Mejorar el desempeo de tu negocio gracias a estrategias asertivas.</li>\n                <li>Implementar acciones de marketing y estrategias comerciales.</li>\n                <li>Tomar decisiones estratgicas basadas en informacin real derivada de las transacciones realizadas en tu\n                    negocio.</li>\n                <li>Identificar cuantos clientes nuevos lograste capturar.</li>\n            </ul>\n    \n            <h3 dir=\"ltr\">Qu te permite?</h3>\n    \n            <ul class=\"list-basic\" dir=\"ltr\">\n                <li>Comparar el rendimiento de la facturacin de tu negocio por periodos y ajustar presupuestos de venta.\n                </li>\n                <li>Adaptar tu modelo de precios, revisando el ticket promedio de tu categora y tu competencia.</li>\n                <li>Reaccionar oportunamente generando estrategias que te ayuden a crecer tu negocio, creando ofertas para\n                    mover tus ventas en das de menor trfico.</li>\n                <li>Ajustar tus horarios de apertura o cierre, los turnos del personal y organizar los horarios de\n                    abastecimiento e inventarios.</li>\n                <li>Crear estrategias pensadas en tu cliente y segmentar tus comunicaciones.</li>\n                <li>Conocer diferentes perfiles de acuerdo con su comportamiento de consumo y adecuar el servicio en tu\n                    punto de venta.</li>\n            </ul>\n    \n        </div>\n        <div id=\"tab3\" class=\"tab-pane\">\n            <h2>Conoce los planes sin costo que tenemos para ti!</h2>\n            <p dir=\"ltr\">Conoce los planes sin costo que tenemos para ti!</p>\n    \n            <h3 dir=\"ltr\">El nico requisito que necesitas para empezar a usar Plink:</h3>\n    \n            <p dir=\"ltr\">Tener el servicio ventas con tarjetas (adquirencia) de las franquicias Visa, MasterCard y/o Amex\n                contratado con Bancolombia, es decir, que los pagos con tarjeta que recibes en tu datfono van a una cuenta\n                Bancolombia.</p>\n    \n            <p dir=\"ltr\"><a\n                    href=\"https://www.grupobancolombia.com/wps/portal/negocios-pymes/productos-financieros/ventas-con-tarjeta\"\n                    title=\"Si quieres saber ms sobre esto ingresa aqu.\" target=\"_blank\"\n                    class=\"btn btnPrincipal btn-icon btn-icon-next\" style=\"height: 42px; max-width:400px;\">Si quieres saber\n                    ms sobre esto ingresa aqu.</a></p>\n    \n            <h3 dir=\"ltr\">Para quin es el Plan Bsico de Plink?</h3>\n    \n            <p dir=\"ltr\">Para comercios que tienen contratado el servicio de ventas con tarjetas Bancolombia,<strong> al\n                    menos para una franquicia.</strong></p>\n    \n            <h3 dir=\"ltr\">Qu informacin encuentras?</h3>\n    \n            <ul class=\"check-list\" dir=\"ltr\">\n                <li>Facturacin del negocio con tarjetas crdito y dbito</li>\n                <li>Das con mayores y menores ventas</li>\n                <li>Demografa detallada de los clientes</li>\n                <li>Descripcin general del tipo de cliente con el ticket o la compra promedio ms alta</li>\n                <li>Descripcin general del tipo de cliente que ms compra en un periodo determinado</li>\n            </ul>\n    \n            <p dir=\"ltr\"><a href=\"https://comercios.plink.com.co/sign_up\" title=\"Regstrate ahora\" target=\"_blank\"\n                    class=\"btn btnPrincipal btn-icon btn-icon-next\" style=\"height: 42px; max-width:400px;\">Regstrate\n                    ahora</a></p>\n    \n            <h3 dir=\"ltr\">Para quin es el Plan Intermedio de Plink?</h3>\n    \n            <p dir=\"ltr\">Para comercios que tienen contratado el servicio de ventas con tarjetas Bancolombia, <strong>para\n                    las tres franquicias.</strong></p>\n    \n            <h3 dir=\"ltr\">Qu informacin encuentras?</h3>\n    \n            <ul class=\"check-list\" dir=\"ltr\">\n                <li>Facturacin del negocio con tarjetas crdito y dbito</li>\n                <li>Das con mayores y menores ventas</li>\n                <li>Demografa detallada de los clientes</li>\n                <li>Descripcin general del tipo de cliente con el ticket o la compra promedio ms alta</li>\n                <li>Descripcin general del tipo de cliente que ms compra en un periodo determinado</li>\n                <li>Informacin comparativa sobre la categora de negocio a la cual pertenece.</li>\n                <li>Hbitos de consumo de los clientes en otras categoras.</li>\n                <li>Mapa de calor (muestra de dnde vienen los clientes).</li>\n                <li>Rango dinmico de perodos de anlisis.</li>\n                <li>Clientes nuevos y recurrentes.</li>\n                <li>Participacin de mercado segn la competencia Horas de mayor y menor cantidad de transacciones en tu\n                    negocio</li>\n            </ul>\n    \n            <p dir=\"ltr\"><a href=\"https://comercios.plink.com.co/sign_up\" title=\"Regstrate ahora\" target=\"_blank\"\n                    class=\"btn btnPrincipal btn-icon btn-icon-next\" style=\"height: 42px; max-width:400px;\">Regstrate\n                    ahora</a></p>\n    \n        </div>\n        <div id=\"tab4\" class=\"tab-pane\">\n            <h2>Tips que te ayudarn a entender en detalle toda la informacin que Plink te brinda.</h2>\n            <p dir=\"ltr\">Con Plink puedes conocer el comportamiento general de tus ventas y el perfil general de tus\n                clientes. Entrando a la seccin inicio tienes toda la informacin que necesitas. Aqu te dejamos algunos\n                consejos:</p>\n    \n            <ul class=\"check-list\" dir=\"ltr\">\n                <li>Compara el rendimiento de la facturacin de tu negocio por periodos y <strong>ajusta presupuestos de\n                        venta.</strong></li>\n                <li><strong>Adapta tu modelo de precios</strong>, revisando el ticket promedio de tu categora y tu\n                    competencia.</li>\n                <li>Reacciona oportunamente generando estrategias que te ayuden a crecer tu negocio, creando ofertas para\n                    <strong>mover tus ventas en das de menor trfico.</strong></li>\n                <li><strong>Ajusta tus horarios</strong> de apertura o cierre,<strong> los turnos del personal</strong> y\n                    organiza los horarios de <strong>abastecimiento e inventarios</strong></li>\n            </ul>\n    \n            <p dir=\"ltr\">Con Plink puedes conocer en detalle cmo son tus clientes. Entrando a la seccin Perfil de Clientes\n                tienes toda la informacin que necesitas. Estos son algunos consejos:</p>\n    \n            <ul class=\"check-list\" dir=\"ltr\">\n                <li>Valida lo que conoces de tus clientes con <strong>datos reales de compra.</strong></li>\n                <li>Crea estrategias pensadas en tu cliente y <strong>segmenta tus comunicaciones.</strong></li>\n                <li>Conoce diferentes perfiles de acuerdo con su comportamiento de consumo y <strong>adecua el servicio en\n                        tu punto de venta.</strong></li>\n                <li><strong>Diversifica tu portafolio</strong> atendiendo las necesidades de tus compradores.</li>\n            </ul>\n    \n            <p dir=\"ltr\">Con Plink puedes saber cmo se comportan tus clientes al comprar. Entrando a la seccin Hbitos de\n                Consumo tienes toda la informacin que necesitas. Sigue estos tips:</p>\n    \n            <ul class=\"check-list\" dir=\"ltr\">\n                <li>Conoce a profundidad tus clientes ms rentables para <strong>adaptar tu comunicacin y tu oferta de\n                        productos o servicios</strong> a sus gustos e intereses.</li>\n                <li>Renueva tu portafolio de productos o servicios para cautivar otros segmentos, en funcin de las\n                    preferencias de tus clientes y crea opciones gama alta o los productos low cost, de acuerdo a sus\n                    gustos.</li>\n                <li>Capacita tu personal para <strong>identificar y consentir a tus clientes ms rentables.</strong></li>\n            </ul>\n    \n            <p dir=\"ltr\">Con Plink puedes compararte con tu categora y tu competencia. Entrando a la seccin Anlisis de\n                Mercado, tienes toda la informacin que necesitas. Mira como sacarle provecho:</p>\n    \n            <ul class=\"check-list\" dir=\"ltr\">\n                <li>Define cmo distribuir tus esfuerzos de mercadeo como volanteo o publicidad exterior en las zonas verdes\n                    del mapa de calor para reforzar tu presencia.</li>\n                <li>Define la cobertura de tus domicilios en zonas rojas del mapa de calor.</li>\n                <li>Elige una zona que sea de inters para expandir tu marca, en caso de buscar abrir franquicias o nuevos\n                    locales.</li>\n            </ul>\n    \n        </div>\n        <div id=\"tab5\" class=\"tab-pane\">\n            <h2>Lo que debes saber</h2>\n            <ul class=\"steps-list\" dir=\"ltr\"><br>\n                <li class=\"step_item\"><span class=\"step_number extrabold color-yellow\">1</span>\n                    <p class=\"step_item_p\">Plink es una idea Bancolombia.</p>\n                </li>\n                <br>\n                <li class=\"step_item\"><span class=\"step_number extrabold color-yellow\">2</span>\n                    <p class=\"step_item_p\">Para disfrutar de este beneficio debes contar con el servicio de ventas con\n                        tarjetas (adquirencia) para alguna de las siguientes franquicias Visa , MasterCard y Amex contratado\n                        por colombia.</p>\n                </li>\n                <li class=\"step_item\"><span class=\"step_number extrabold color-yellow\">3</span>\n                    <p class=\"step_item_p\">La informacin relacionada a las ventas y facturacin corresponde a las\n                        transacciones realizadas en los datfonos de tu comercio, derivadas del servicio de ventas con\n                        tarjetas (adquirencia), por este motivo entre ms tiempo lleves disfrutndolo, ms informacin\n                        tendremos para entregarte.</p>\n                </li>\n                <br>\n                <li class=\"step_item\"><span class=\"step_number extrabold color-yellow\">4</span>\n                    <p class=\"step_item_p\">Toda la informacin del cliente final corresponde a clientes con Tarjeta\n                        Bancolombia.</p>\n                </li>\n                <br>\n                <li class=\"step_item\"><span class=\"step_number extrabold color-yellow\">5</span>\n                    <p class=\"step_item_p\">Esta informacin es agregada y anonimizada, por este motivo Plink no hace mencin\n                        de marcas especficas.</p>\n                </li>\n                <br>\n                <li class=\"step_item\"><span class=\"step_number extrabold color-yellow\">6</span>\n                    <p class=\"step_item_p\">La informacin se presenta mensualmente en la plataforma web mediante grficas.\n                    </p>\n                </li>\n            </ul>\n    \n        </div>\n    </div>\n</div>");

